import service from "@/utils/http"


export const export_private_ip = (domain_id_list: String) => {
    return service.post('/common/ip/export_private_ip', {domain_id_list:domain_id_list})
}

export const export_ip_port = (domain_id_list: String) => {
    return service.post('/common/ip/export_ip_port', {domain_id_list:domain_id_list})
}

export const export_website = (domain_id_list: String) => {
    return service.post('/common/website/export_website', {domain_id_list:domain_id_list})
}

export const export_subdomain = (domain_id_list: String) => {
    return service.post('/common/subdomain/export_subdomain', {domain_id_list:domain_id_list})
}

export const get_detail = (data: any) => {
    const refactor_data = {page_num: 1, page_size: 10, query: '', search_type: '', current_domain_id: '', detail_type: ''}
    refactor_data.page_num = data.page_num
    refactor_data.page_size = data.page_size
    refactor_data.search_type = data.search_type
    refactor_data.current_domain_id = data.current_domain_id
    refactor_data.detail_type = data.detail_type
    if(data.query == ''){
        refactor_data.query = 'null'
    }else{
        refactor_data.query = data.query
    }
    return service.get(`/detail/${refactor_data.detail_type}/${refactor_data.search_type}/${refactor_data.current_domain_id}/${refactor_data.page_num}/${refactor_data.page_size}/${refactor_data.query}`)
}

export const get_overview = (current_domain_id: string) => {
    return service.get(`/detail/overview/${current_domain_id}`)
}

